body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h4 {
  margin-bottom: 20px;
}

p {
  margin: 0px;
}

ul {
  margin: 10px 0px;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

#root {
  width: 100%;
  height: 100%;
}

.bottomBar {
  margin-top: auto;
  font-weight: 400;
}

.TopNavBar {
  width: 100%;
  margin-bottom: 10px;
}

.TopNavBar .active {
  color: darkgreen !important;
}

.navbar {
  width: fit-content
}

.flexTopNavBar {
  display: flex;
  width: 100%;
  justify-content: center;
  align-content: center;
}

@media only screen and (max-width: 992px) {
  .navbar {

    width: 100%;

    padding: 10px 20px;
  }
}

.pageContainer {
  width: 100%;
  /* height: 100%; */
  /* min-height: calc(100% - (76px + 55.5px)); */

}

.dropdown-menu.show {
  padding: 10px;
}

.linkPhone,
.linkMail {
  color: darkred;
}

.linkPhone:hover,
.linkMail:hover {
  color: rgb(51, 0, 0);
}

.active {
  color: rgba(0, 0, 0, .55) !important;
}

.customActive1,
.customActive2 {
  color: darkgreen !important
}

